
import robonomics from "./robonomics";
import Video from "./components/Video.vue";
import Account from "./components/Account.vue";
import Sender from "./components/Sender.vue";

export default {
  name: "App",
  components: {
    Video,
    Account,
    Sender
  },
  data() {
    return {
      isReady: false
    };
  },
  async created() {
    await robonomics.run();
    this.isReady = true;
  }
};
